// Chaching needed Dom Elements
const stopwatchHours = window.document.querySelector('.stopwatch-hours');
const stopwatchMins = window.document.querySelector('.stopwatch-mins');
const stopwatchSecs = window.document.querySelector('.stopwatch-secs');

const stopwatchStartBtn = window.document.querySelector('.stopwatch-start-btn');
const stopwatchPauseBtn = window.document.querySelector('.stopwatch-pause-btn');
const stopwatchResetBtn = window.document.querySelector('.stopwatch-reset-btn');

// local variable for StopWatch scope
let time = 0;
let timer;

const stopwatchDisplay = () => {
  const hours = Math.floor(time / 3600);
  const minutes = Math.floor((time - hours * 3600) / 60);
  const seconds = Math.floor(time - hours * 3600 - minutes * 60);

  stopwatchHours.textContent = `${hours}`.padStart(2, '0');
  stopwatchMins.textContent = `${minutes}`.padStart(2, '0');
  stopwatchSecs.textContent = `${seconds}`.padStart(2, '0');
};

const disableBtn = (btn) => btn.setAttribute('disabled', 'disabled');

const disableBtns = (...btns) =>
  btns.forEach((btn) => btn.setAttribute('disabled', 'disabled'));

const enableBtn = (btn) => btn.removeAttribute('disabled');

const enableBtns = (...btns) =>
  btns.forEach((btn) => btn.removeAttribute('disabled', 'disabled'));

const startStopwatch = () => {
  disableBtn(stopwatchStartBtn);
  enableBtns(stopwatchPauseBtn, stopwatchResetBtn);

  if (stopwatchStartBtn.textContent === 'Continue')
    stopwatchStartBtn.textContent = 'Start';

  timer = window.setInterval(() => {
    time += 1;
    stopwatchDisplay();
  }, 1000);
};

const pauseStopwatch = () => {
  disableBtn(stopwatchPauseBtn);
  enableBtn(stopwatchStartBtn);

  window.clearInterval(timer);
  stopwatchStartBtn.textContent = 'Continue';
};

const resetStopwatch = () => {
  disableBtns(stopwatchPauseBtn, stopwatchResetBtn);
  enableBtn(stopwatchStartBtn);

  window.clearInterval(timer);
  if (stopwatchStartBtn.textContent === 'Continue') {
    stopwatchStartBtn.textContent = 'Start';
  }
  time = 0;
  stopwatchDisplay();
};

// Add click event to stopwatchStartBtn to handle  stopwatch start task.
stopwatchStartBtn.addEventListener('click', startStopwatch);

// Add click event to stopwatchPauseBtn to handle stopwatch pause task.
stopwatchPauseBtn.addEventListener('click', pauseStopwatch);

// Add click event to stopwatchStartBtn to handle stopwatch reset task.
stopwatchResetBtn.addEventListener('click', resetStopwatch);

export const stopwatchInit = () => {
  disableBtns(stopwatchPauseBtn, stopwatchResetBtn);
};
